.welcome-box{

    border-radius: 15px;
    position: relative;
    top: 10px;
    left: 5%;
}
.welcome-box-text {
    text-align: center;
    color: black;
    font-size: 30;
    font-weight: 400;
    word-wrap: break-word;
    
}

.imgZoom {
    transition: transform 0.2s ease;
}

.imgZoom:hover {
    transform: scale(1.1);
}

.courses-btn {
    height: 70%;
}

.modal {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    box-shadow: rgba(100, 100, 111, 0.3) 0px 7px 29px 0px;
    background-color:rgba(255, 238, 164);
    border: 2px solid rgb(211, 173, 5);
    border-radius: 12px;
    position:  absolute;
    width: 40%;
    height: 70%;
    top: 100px;
    left: calc(50% - 125px);
    bottom: 70px;
}
